<template>
  <div class="pageContent">
    <el-card shadow="always">
      <el-form ref="form" :model="form" label-width="140px">
        <el-form-item label="待付款订单超时：">
          <el-input-number 
            v-model="form.closeOvertime"
            step-strictly
            :controls="false"
            placeholder="请输入分钟数"
          >
          </el-input-number>
          <span>分未付款，订单自动关闭</span>
        </el-form-item>
        <el-form-item label="订单确认收货超过：">
          <el-input-number 
            v-model="form.finishOvertime"
            step-strictly
            :controls="false"
            placeholder="请输入天数"
          >
          </el-input-number>
          <span>天未收货，订单自动完成</span>
        </el-form-item>
        <el-form-item label="订单完成超过：">
          <el-input-number 
            v-model="form.rejectOvertime"
            step-strictly
            :controls="false"
            placeholder="请输入天数"
          >
          </el-input-number>
          <span>天自动结束交易，不能申请售后</span>
        </el-form-item>
        <el-form-item label="订单确认收货：">
          <el-input-number 
            v-model="form.assesOvertime"
            step-strictly
            :controls="false"
            placeholder="请输入天数"
          >
          </el-input-number>
          <span>天自动五星好评</span>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="changeData">保 存</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopConfigInfo } from '@/api/logistics/logisticsTemplate'
import { updateShopConfigInfo } from '@/api/order/orderConfig'
export default {
  data(){
    return{
      form:{}
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      getShopConfigInfo(this.shop.shopId).then(res=>{
        console.log('订单配置',res)
        this.form = res.data
      })
    },
    changeData(){
      if(!this.form.closeOvertime){
        this.$message.error('请输入正确的待付款订单超时分钟数');
        return;
      }
      if(!this.form.finishOvertime){
        this.$message.error('请输入正确的订单确认收货天数');
        return;
      }
      if(!this.form.rejectOvertime){
        this.$message.error('请输入正确的订单售后天数');
        return;
      }
      if(!this.form.assesOvertime){
        this.$message.error('请输入正确的订单评价天数');
        return;
      }
      console.log('成功了 请求的参数',this.form)
      updateShopConfigInfo(this.form).then(res=>{
        console.log('更新配置',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>

</style>